/** Quick Notes Styles **/

/* Import Oleo Script Font */
@import url("https://fonts.googleapis.com/css2?family=Oleo+Script:wght@400;700&display=swap");

/* Import Raleway Font */
@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

/* Variable Declarations */
:root {
  --note-background-primary: white;
  --note-header-text-color: #333333;
  --note-header-text-color-hover: white;
  --note-text-color-primary: #333333;

  --note-text-color-edit: #828282;

  /* Color Definitions */
  --color-black: black;
  --color-black-hover: #151515;
  --color-white: white;
  --color-white-hover: #ededed;
  --color-grey: #b4b4b4;
  --color-grey-dark: #828282;

  --color-red: #eb5757;
  --color-red-dark: #cf4242;
  --color-orange: #f2994a;
  --color-orange-dark: #e98034;
  --color-yellow: #f2c94c;
  --color-yellow-dark: #edac2f;
  --color-green: #27ae60;
  --color-green-dark: #2b9843;
  --color-blue: #2f80ed;
  --color-blue-dark: #2c62cd;
  --color-purple: #bb6bd9;
  --color-purple-dark: #964cc3;
  --color-pink: #f035a5;
  --color-cyan: #31c6da;
  --color-lemon: #faff00;
  --color-lime: #58d744;
}

/* Quick Notes List Styles */

.quicknotes-list {
  display: grid;
  /* grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); */
  grid-template-columns: repeat(auto-fit, 230px);
  grid-auto-rows: auto;
  max-width: 100vw;
  row-gap: 1rem;
  column-gap: 1rem;
  z-index: 1;
}

/* General Quick Note Styles */
.quicknote {
  background-color: var(--note-background-primary);
  width: 230px;
  height: fit-content;
  justify-self: center;
  font-size: 13px;
  font-family: "Source Sans Pro", sans-serif !important;
}

.quicknote-title {
  border: none;
  background-color: inherit;
  font-family: "Source Sans Pro", sans-serif;
  color: var(--note-header-text-color);
}

.quicknote-title::placeholder {
  color: var(--note-header-text-color);
}

.quicknote-content {
  height: 170px;
}

.quicknote textarea {
  border: none;
  resize: none;
  background-color: inherit;
  font-size: 13px;
  font-family: "Source Sans Pro", sans-serif !important;
  font-weight: 500;
}

/* Scrollbar Styles */
.quicknote textarea::-webkit-scrollbar {
  width: 5px;
}

.quicknote textarea::-webkit-scrollbar-thumb {
  background: var(--color-grey-dark);
}

.quicknote textarea::-webkit-scrollbar-thumb:hover {
  cursor: default;
}